import { Injectable } from '@angular/core';
import { Hero } from '../../model/hero';
import { HEROS } from '../../model/mock.hero';
import { observable, of, Observable, from } from 'rxjs';
import { MessageService } from '../message/message.service';
import { filter, catchError, tap } from 'rxjs/operators';
import { HttpClient, HttpHeaders } from '@angular/common/http';
@Injectable({
  providedIn: 'root'
})
export class HeroService {

  private heroesUrl = 'api/heroes';

  constructor(private messagesService: MessageService, private httpClient: HttpClient) { }

  public getHeroes(): Observable<Hero[]> {
    // return of(HEROS);
    return this.httpClient.get<Hero[]>(this.heroesUrl).pipe(
      tap(_ => this.log('getHeroes')),
      catchError(this.handleError<Hero[]>('get heres', []))
    );
  }

  private handleError<T>(operator: string = 'operation', result?: T) {
    return (error: any): Observable<T> => {
      console.error(error);
      this.log(`get heroes error : ${error.message}`);
      return of(result as T);
    };
  }
  getHero(id: number): Observable<Hero> {
    // return from(HEROS).pipe(
    //   filter(v => v.id === id)
    // );

    const url = `${this.heroesUrl}/${id}`;
    return this.httpClient.get<Hero>(url).pipe(
      tap(v => console.log(v)),
      catchError(this.handleError<Hero>('getHeroById', null))
    );
  }

  public updateHero(hero: Hero): Observable<any> {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };
    return this.httpClient.put<any>(this.heroesUrl, hero, httpOptions).pipe(
      tap(_ => this.log(`update hero id: ${hero.id}`)),
      catchError(this.handleError<string>('update hero Error', 'update hero error'))
    );
  }

  addHero(hero: Hero): Observable<Hero> {
    const httpOptions = {
      headers: new HttpHeaders({ 'Content-Type': 'application/json' })
    };
    return this.httpClient.post<Hero>(this.heroesUrl, hero, httpOptions).pipe(
      tap(v => console.log(v)),
      catchError(this.handleError<Hero>('new hero', null))
    );
  }

  deleteHero(hero: Hero | number): Observable<Hero> {
    const id = typeof hero === 'number' ? hero : hero.id;
    const url = `${this.heroesUrl}/${id}`;
    return this.httpClient.delete<Hero>(url).pipe(
      tap(_ => this.log('delete hero')),
      catchError(this.handleError<Hero>('delete hero', null))
    );
  }

  searchHeroes(term: string): Observable<Hero[]> {
    if (!term.trim()) {
      return of([]);
    }
    const url = `${this.heroesUrl}/?name=${term}`;
    return this.httpClient.get<Hero[]>(url).pipe(
      tap(v => console.log(v)),
      catchError(this.handleError<Hero[]>('search hero', []))
    );
  }

  private log(logMsg: string) {
    this.messagesService.add(logMsg);
  }

}
