import { Component, OnInit, Input } from '@angular/core';
import { Hero } from 'src/app/model/hero';
import { ActivatedRoute } from '@angular/router';
import { HeroService } from 'src/app/service/hero/hero.service';
import { Location } from '@angular/common';
import { Observable } from 'rxjs';
import { tap, catchError } from 'rxjs/operators';

@Component({
  selector: 'app-heros-detail',
  templateUrl: './heros-detail.component.html',
  styleUrls: ['./heros-detail.component.css']
})
export class HerosDetailComponent implements OnInit {

  // @Input() public hero: Hero;
  public hero: Hero;

  constructor(
    private route: ActivatedRoute,
    private heroService: HeroService,
    private location: Location
  ) { }

  ngOnInit() {
    this.getHero();
  }

  public getHero() {
    const id: number = +this.route.snapshot.paramMap.get('id');
    this.heroService.getHero(id).subscribe(v => this.hero = v);
  }

  public goBack() {
    this.location.back();
  }

  public save() {
    this.heroService.updateHero(this.hero).pipe(
      tap(v => console.log(v))
    ).subscribe(v => {
      console.log(v);
      this.goBack();
    });
  }
}
